@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/pt-mono-regular.woff2") format("woff2"), url("../fonts/pt-mono-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff"); }

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff"); }

html {
  height: 100%;
  box-sizing: border-box; }

*, *::before, *::after {
  box-sizing: inherit; }

body {
  margin: 0;
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 0; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header {
  position: absolute;
  right: 50%;
  transform: translateX(50%);
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 1511px;
  min-width: 320px;
  padding-top: 33px;
  padding-left: 40px;
  padding-right: 40px; }

.page-header__wrapper-contact-group {
  display: flex;
  align-items: flex-start; }

.page-header__wrapper-links {
  display: flex;
  flex-wrap: wrap; }

.page-header__link {
  margin-top: 17px;
  margin-right: 50px;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  font-family: "PT Mono", "Courier New", monospace;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #ffffff; }
  .page-header__link--email {
    margin-top: 16px;
    margin-right: 90px; }
  .page-header__link:hover, .page-header__link:focus {
    color: #b3b3b3; }

.page-header__link-icon {
  margin-right: 15px;
  fill: #00b2ff; }
  .page-header__link-icon--email {
    margin-right: 19px; }

.page-header__link:hover .page-header__link-icon,
.page-header__link:focus .page-header__link-icon {
  fill: #006b99; }

.page-header__order-phone-call-link {
  margin-top: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 180px;
  max-width: 360px;
  height: 45px;
  padding: 10px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  background-color: transparent;
  border: 1px solid #ffffff;
  border-radius: 5px; }
  .page-header__order-phone-call-link:hover, .page-header__order-phone-call-link:focus {
    color: #b3b3b3; }

@media (max-width: 1023px) {
  .page-header {
    padding-top: 20px;
    padding-left: 25px;
    padding-right: 0; }
  .page-header__link {
    margin-top: 12px;
    margin-right: 10px; }
    .page-header__link--email {
      margin-right: 15px; }
  .page-header__link-icon {
    width: 24px;
    height: 24px; }
    .page-header__link-icon--email {
      width: 26px;
      height: 19px; }
  .page-header__link-text {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden; }
  .page-header__order-phone-call-link {
    display: none; } }

@media (max-width: 767px) {
  .page-header {
    padding-top: 17px;
    padding-left: 18px; }
  .page-header__link {
    margin-top: 4px;
    margin-right: 2px; }
    .page-header__link--email {
      margin-right: 3px; } }

.logo-group {
  margin-right: 30px;
  display: flex; }

.logo-group__logo-text {
  margin-left: 13px;
  min-width: 160px; }

.logo-group__logo-text p {
  margin-top: 5px;
  margin-bottom: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 25px;
  line-height: 29px;
  color: #ffffff; }

.logo-group__logo-text p + p {
  margin-top: 2px;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: #788891; }

@media (max-width: 1023px) {
  .logo-group__logo-text {
    margin-left: 17px; } }

@media (max-width: 767px) {
  .logo-group__logo--header {
    width: 38px;
    height: 38px; }
  .logo-group__logo-text--header {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden; } }

.main-screen {
  min-height: 878px;
  background: #1b2d37 url("../img/background-main-screen.png") no-repeat center center;
  background-size: cover; }
